
  import { defineComponent, ref, watch } from 'vue'

  export default defineComponent({
    name: 'CustomTextArea',
    props: {
      charLimit: {
        type: Number,
        default: 500,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      input: {
        type: String,
        default: '',
      },
      name: {
        type: String,
        required: true,
      },
    },
    emits: ['updateValue'],
    setup(props, { emit }) {
      let text = ref('')

      function updateText(event: Event & { target: HTMLInputElement }) {
        emit('updateValue', event.target.value)
      }

      watch(
        () => props.input,
        (value) => {
          text.value = value
        }
      )

      return { text, updateText }
    },
  })
